.site-footer{
  background: $light-black;
  color: $white;
  padding: 60px 0 30px;
  @media screen and (max-width: 600px) {
    padding: 30px 0;
  }
  a{
    color: $white;
    transition: color .3s ease;
    text-decoration: none;
    &:hover,
    &:focus{
      color: $primary_color;
    }
  }
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .contact-info{
    margin-bottom: 20px;
    li{
      margin-bottom: 5px;
      display: flex;
      flex-wrap: wrap;
      span{
        &.title{
          width: 75px;
          padding-right: 10px;
          text-align: right;
        }
        &.description{
          width: calc(100% - 75px);
        }
      }
    }
  }
  &__primary{
   .col-contact,
    .col-menu{
     width: 25%;
     @media #{$tablet-portrait} {
       width: 33.33%;
     }
     @media #{$phone} {
       width: 100%;
     }
   }
    .col-form{
      width: 50%;
      @media #{$tablet-portrait} {
        width: 33.33%;
      }
      @media #{$phone} {
        width: 100%;
      }
    }
    .col{
      margin-bottom: 15px;
      padding: 0 15px;
      @media #{$phone} {
        margin-bottom: 30px;
      }
      &__title{
        @include rem(font-size, 24px);
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid;
      }
    }
  }
  &__secondary{
    @include rem(font-size, 15px);
    .flex{
      justify-content: space-between;
    }
    p{
      margin-bottom: 0;
    }
    .container{
      padding: 30px 15px 0;
      border-top: 1px solid #3f3b3b;
      margin-top: 30px;
      @media screen and (max-width: 600px) {
        margin-top: 0;
        padding-top: 20px;
      }
    }
  }
}
