/* Hero Block */
.hero-block{
  position: relative;
  .flex{
    min-height: inherit;
    align-items: center;
    justify-content: center;
  }
  .hero-content{
    position: relative;
    z-index: 2;
    color: $white;
    @include rem(font-size, 18px);
    p{
      margin: 0;
    }
  }
  h1{
    color: $white;
    margin-bottom: 5px;
  }
  .button{
    margin-top: 15px;
  }
  &--inner{
    min-height: 380px;
    background-position: center center;
    background-size: cover;
    position: relative;
    @media #{$tablet-portrait} {
      //min-height: 450px;
    }
    @media #{$phone} {
      min-height: 300px;
    }
    &:after{
      content: "";
      background: rgba(0, 0, 0, .5);
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
    .hero-content{
      margin-top: 120px;
      @media #{$phone} {
        margin-top: 0;
      }
    }
  }
}

.hero-slider{
  min-height: 100vh;
  @media screen and (min-width: 1400px) {
    min-height: 700px;
  }
  @media #{$phone} {
    min-height: calc(100vh - 180px);
  }
  @media screen and (max-width: 670px) {
    min-height: calc(100vh - 200px);
  }
  @media screen and (max-width: 499px) {
    min-height: 300px;
  }
  .slick-list,
  .slick-track,
  .slick-slide,
  .slick-slide > div {
    min-height: inherit;
  }
  .slick-slide{
    padding: 0;
  }
  &__item{
    min-height: inherit;
    background-position: center center;
    background-size: cover;
    position: relative;
    &:after{
      content: "";
      background: rgba(0, 0, 0, .5);
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
    @media #{$header} {
      //min-height: 230px;
    }
  }
  .slick-arrow{
    display:none !important;
    z-index: 5;
    width: 32px;
    height: 30px;
    &:before{
      @include rem(font-size, 34px);
      @media screen and (max-width: 1200px) {
        @include rem(font-size, 22px);
        width: 20px;
        height: 20px;
      }
    }
  }
  &:hover{
    .slick-arrow{
      display:block !important;
    }
  }
  .slick-prev{
    left: 25px;
  }
  .slick-next{
    right: 25px;
  }
  .hero-content{
    padding-top: 100px;
    text-align: center;
    @media #{$phone} {
      padding-top: 0;
    }
  }
}