.form-wrapper{
  .form-body{
    .field{
      margin-bottom: 15px;
        > label{
          margin: 0;
          @include rem(font-size, 16px);
          font-weight: 600;
          //color: #262626;
          letter-spacing: 0.1px;
        }
      &.hide-label{
        label{
          display: none;
        }
      }
      input,
      textarea,
      select{
        margin: 0;
        width: 100%;
        padding: 12px 15px 10px;
        color: #262626;
        line-height: 1.38;
        @include rem(font-size, 16px);
        height: inherit;
        box-shadow: none;
        border-radius: 3px;
        border: 1px solid transparent;
        background: $light_grey;
        -webkit-appearance: none;
        transition: border .2s ease;
        .site-footer &{
          background: $white;
        }
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: $light_white;
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color: $light_white;
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color: $light_white;
        }
        &:-moz-placeholder { /* Firefox 18- */
          color: $light_white;
        }
        &:focus{
          border: solid 1px $primary_color;
        }
      }
      select{
        background: rgba(204, 204, 204, .26) url("../assets/images/select-arrow.png") no-repeat 98% 17px;
      }
      .checkbox {
        position: relative;
        padding-left:35px;
          label {
            font-weight: 600;
            &:after {
              content:"";
              background:rgba(204, 204, 204, .6);
              width:24px;
              height:24px;
              display:block;
              position: absolute;
              left:0;
              top:0;
              border-radius:3px;
              z-index: 0;
            }
            &:before {
              content:"";
              display:block;
              position: absolute;
              width: 6px;
              height: 15px;
              border: solid $tertiary_color;
              border-width: 0 3px 3px 0;
              left: 9px;
              top: 4px;
              transform: rotate(45deg);
              opacity:0;
              z-index:1;
              transition:all 100ms;
            }
          }

      }

      input[type="checkbox"] {
        opacity:0;
        outline:none;
        z-index:100;
        width:27px;
        height:27px;
        top:0;
        left:0;
        position: absolute;
        appearance: none;
        cursor: pointer;
        + label {
          cursor: pointer;
        }
        &:checked + label {
          &:before {
            opacity:1;
            top: 3px;
          }
        }
      }
      &.error{
        input,
        textarea{
          border: solid 1px $alert-color-border;
          background: $alert-color-bg;
          &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: $alert-color;
          }
          &::-moz-placeholder { /* Firefox 19+ */
            color: $alert-color;
          }
          &:-ms-input-placeholder { /* IE 10+ */
            color: $alert-color;
          }
          &:-moz-placeholder { /* Firefox 18- */
            color: $alert-color;
          }
        }
      }
    }
    .col-half{
      width: 50%;
      @media #{$tablet-portrait} {
        width: 100%;
      }
      &:first-child{
        padding-right: 15px;
        @media #{$tablet-portrait} {
          padding-right: 0;
        }
      }
      &:last-child{
        padding-left: 15px;
        @media #{$tablet-portrait} {
          padding-left: 0;
        }
      }
    }
  }
}