.review-block{
  padding: 60px 0;
  text-align: center;
  background: $tertiary_color;
  color: $black;
  @media #{$phone} {
   padding: 40px 0;
  }
  a{
    color: $black;
    transition: color .2s ease;
    &:hover{
      color: $primary_color_dark;
    }
  }
}
.testimonial-info{
  .image-holder{
    height: 150px;
    width: 150px;
    border-radius: 50%;
    overflow: hidden;
    text-align: center;
    margin: 0 auto 10px;
    img{
      display: block;
    }
  }
  span{
    display: block;
    font-weight: 700;
    &.name{
      font-weight: 700;
      font-family: $font__main;
    }
  }
}
.testimonial-slider{
  margin: 0 auto;
  max-width: 80%;
  @media #{$phone} {
    max-width: 100%;
  }
  &.slick-slider{
    margin-bottom: 60px;
  }
  .slick-dots{
    bottom: -40px;
    li{
      button{
        height: 10px;
        width: 10px;
        border-radius: 50%;
        border: 1px solid $black;
        &:before{
          display: none;
        }
      }
      &.slick-active{
        button{
          background: $black;
        }
      }
    }
  }
}
.testimonial-list{

}
.testimonial-item{
  background: $white;
  padding: 60px 0;
  .container{
    max-width: 1024px;
  }
  &:nth-child(2n - 1){
    .flex{
      @media #{$phone} {
        flex-direction: column-reverse;
      }
    }
    .description{
      padding: 0 60px 0 15px;
      @media #{$phone} {
        padding: 15px 0;
      }
    }
  }
  &:nth-child(2n){
    background: $light_grey;
    .description{
      padding: 0 15px 0 60px;
      @media #{$phone} {
        padding: 15px 0;
      }
    }
    .flex{
      flex-direction: row-reverse;
      @media #{$phone} {
        flex-direction: column-reverse;
      }
    }
  }
  .description{
    width: calc(100% - 220px);
    @media #{$phone} {
      width: 100%;
    }
    blockquote{
      margin: 0;
      padding: 0 35px;
      position: relative;
      &:before,
      &:after{
        position: absolute;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: #cacdd1;
        @include rem(font-size, 24px);
        line-height: 1;
      }
      &:before{
        content: "\f10d";
        top: 0;
        left: 0;
      }
      &:after{
        content: "\f10e";
        right: 0;
        bottom: 0;
      }
    }
    .testimonial-info{
      color: $grey;
      padding: 20px 35px 0;
    }
  }
  .round-holder{
    width: 220px;
    height: 220px;
    border-radius: 50%;
    overflow: hidden;
    text-align: center;
    margin: 0;
    @media #{$phone} {
      margin: 0 15px 20px;
    }
  }
}