/* Primary Navigation */
.primary-navigation{
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease;
}

#mobile-nav-trigger {
  opacity: 0;
  visibility: hidden;
  display: none;
  width: 24px;
  height: 18px;
  margin-left: 20px;
  top: 30px;
  left: 15px;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  float: right;
  z-index: 5;
  border: none;
  background: none;
  box-shadow: none;
  @media #{$tablet-portrait} {
    opacity: 1;
    visibility: visible;
    display: inline-block;
  }
  span{
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background-color: $white;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
    &:nth-child(1){
      top: 0;
    }
    &:nth-child(2){
      top: 7px;
    }
    &:nth-child(3){
      top: 14px;
    }
    &.screen-reader-text{
      border: 0;
      clip: rect(0 0 0 0);
      height: 1px;
      top: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px;
    }
  }
  &.open{
    opacity: 1;
    visibility: visible;
    span{
      &:nth-child(1){
        top: 10px;
        transform: rotate(135deg);
      }
      &:nth-child(2){
        opacity: 0;
        left: -30px;
        visibility: hidden;
      }
      &:nth-child(3){
        top: 10px;
        transform: rotate(-135deg);
    }
    }
  }
}

#primary-nav-button {
  background: transparent;
  display: none;
  border: 1px solid $white;
  //position: absolute;
  margin-left: 15px;
  bottom: 32px;
  left: 15px;
  padding: 0;
  text-decoration: none;
  color: $white;
  text-align: center;
  font-weight: bold;
  font-size: 0;
  &:hover{
    background: rgba(0,0,0,0.05);
  }
  &.selected{
    background: rgba(0,0,0,0.1);
  }
  &:before{
    /* content: '☰'; */
    content: '\2261';
    display: block;
    font-size: 36px;
    font-style: normal;
    font-weight: normal;
    line-height: 1.05;
    height: 1em;
    width: 1em;
    color: inherit;
    speak: none;
  }
}


.menu li {
  float: left;
  position: relative;
  &:last-child{
    margin-right: 0;
  }
  .sub-menu{
    li{
      width: 100%;
      a{
        display: block;
        text-decoration: none;
      }
    }
  }
}

#primary-nav {
  ul{
    padding: 0;
  }
  li {
    a {
      color: $white;
      font-weight: 600;
      text-transform: uppercase;
      padding: 10px 14px;
      display: block;
      font-family: $font__main;
      letter-spacing: 1.5px;
      @media screen and (min-width: 1023px ) and (max-width: 1199px){
        @include rem(font-size, 14px);
        padding: 10px 7px;
      }
      .is-sticky & {
        color: $black;
        padding: 10px 12px;
        @include rem(font-size, 15px);
        @media screen and (min-width: 1023px ) and (max-width: 1199px){
          //@include rem(font-size, 14px);
          padding: 10px 5px;
        }
      }
    }
    &.home-menu {
      a{
        letter-spacing: normal;
      }
      span{
        font-size: 0;
        text-indent: -9999em;
      }
    }
    &.active > a,
    > a:hover,
    &.selected > a{
      background: $primary_color;
      color: $white;
    }
    &.menu-btn{
      .button{
        &:hover,
        &:focus{
          color: $tertiary_color;
        }
      }
      &.active{
        .button{
          color: $tertiary_color;
          background: $white;
        }
      }
    }
  }
}


.downarrow {
  background: none;
  display: inline-block;
  padding: 0;
  text-align: center;
  min-width: 3px;
  &:before {
    content: '\25be';
    color: inherit;
    display: block;
    font-family: sans-serif;
    font-size: 1em;
    line-height: 1.1;
    width: 1em;
    height: 1em;
  }
}

.sub-menu .downarrow {
  position: absolute;
  right: 0;
  padding-right: 10px;
}

.menu .sub-menu {
  display: none;
  position: absolute;
  left: 0;
  max-height: 1000px;
}

.menu .sub-menu.hide {
  display: none;
}

#primary-nav {
  .sub-menu {
    background: $primary_color;
    min-width: 200px;
    z-index: 200;
    li{
      border-bottom: 1px solid rgba($white, .5);
      a{
        text-transform: none;
        .is-sticky & {
          color: $white;
        }
        &:hover{
          background: #dd8017;
        }
      }
      &:last-child{
        border-bottom: 0;
      }
    }
    .downarrow {
      &:before{
        content: '\25b8';
      }
    }
  }
  &.mobile {
    display: none;
    position: absolute;
    top: 100%;
    background: $primary_color;
    width: 100%;
    left: 0;
    li {
      width: 100%;
      margin: 0;
      border-bottom: 1px solid rgba($white, .5);
      &.selected {
        > a {
          border-bottom: 1px solid rgba($white, .5);
        }
      }
      &:last-child{
        border: none;
      }
      &.active{
        a{
          background: $primary_color_dark;
        }
      }
    }
    .sub-menu {
      float: left;
      position: relative;
      width: 100%;
      li{
        a {
          padding-left: 7%;
        }
      }
      .sub-menu {
        li{
          a {
            padding-left: 9%;
          }
        }
        .sub-menu {
          li {
            a {
              padding-left: 11%;
            }
          }
        }
      }
    }
  }
}

.mobile .downarrow,
.mobile .sub-menu .downarrow {
  position: absolute;
  right: 0;
  padding-right: 5%;
}

#primary-nav.mobile .sub-menu .downarrow:before {
  content: '\25be';
}
#primary-nav-button.mobile {
  display: inline-block;
  @media screen and (max-width: 670px) {
    position: absolute;
    height: 40px;
    top: 45px;
    margin: 0;
  }
}
