.intro-block{
  padding: 60px 0;
  @media #{$phone} {
    padding: 40px 0;
  }
  &__description,
  &__media{
    width: 50%;
    @media #{$phone} {
      width: 100%;
    }
  }
  &__media{
    @media #{$phone} {
      margin-bottom: 15px;
    }
  }
  > .container{
    > .row.flex{
      @media #{$phone} {
        flex-direction: column-reverse;
      }
    }
  }
  &--secondary{
    background: $light_grey;
  }
}
.image-frame{
  padding: 5px;
  border-radius: 7px;
  background: $light_grey;
  margin: 0;
  img{
    display: block;
    border-radius: 7px;
  }
}

.listing-primary{
  background: $light_grey;
  padding: 60px 0;
  @media #{$phone} {
    padding: 40px 0;
  }
  &__intro{
    text-align: center;
    max-width: 75%;
    margin: 0 auto 35px;
    @media #{$phone} {
     max-width: 100%;
    }
  }
  .section-title{
    @include rem(font-size, 42px);
    margin-bottom: 5px;
    @media #{$phone} {
      @include rem(font-size, 32px);
    }
  }
  .card{
    &__inner{
      padding: 15px;
      background: $light_grey;
      height: 100%;
      box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.2);
      h3{
        @include rem(font-size, 20px);
        margin-bottom: 10px;
        font-weight: 600;
      }
      .image-holder{
        margin: 0;
        height: 175px;
        padding: 5px;
        border-radius: 5px;
        background: $white;
        img{
          object-fit: cover;
          height: 100%;
          width: 100%;
          border-radius: 5px;
        }
      }
      .description{
        padding: 10px 0 0;
      }
    }
  }
}

.listing-secondary{
  background: $white;
  padding: 60px 0;
  @media #{$phone} {
    padding: 40px 0;
  }
  h3{
    margin-bottom: 35px;
    @media #{$phone} {
      margin-bottom: 20px;
    }
  }
}
.product-slider{
  text-align: center;
  .slick-slide{
    padding: 0 15px;
    .image-holder{
      margin: 0 0 10px;
      height: 470px;
      img{
        margin: 0 auto;
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
  }
  &__item{
    h4{
      @include rem(font-size, 22px);
    }
  }
  .slick-arrow{
    &:before{
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      color: $tertiary_color;
      @include rem(font-size, 34px);
    }
    &.slick-prev{
      &:before{
        content: "\f104";
      }
      @media screen and (max-width: 1300px) {
        left: 0;
      }
    }
    &.slick-next{
      &:before{
        content: "\f105";
      }
      @media screen and (max-width: 1300px) {
        right: 0;
      }
    }
  }
}

.primary-block{
  padding: 70px 0 50px;
  @media #{$phone} {
    padding: 40px 0;
  }
  &__intro{
    max-width: 75%;
    margin-bottom: 40px;
    @media #{$tablet-portrait} {
      max-width: 100%;
    }
  }
  .image-holder{
    height: 300px;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    margin: 0;
    border: 7px solid $light_grey;
    border-radius: 7px;
    display: block;
    @media #{$phone-portrait} {
      height: 240px;
    }
  }
  &.bg-colored{
    .image-holder{
      border-color: $white;
    }
  }
  &--alt{
    .three-col{
      .col{
        margin-bottom: 35px;
      }
    }
    .image-holder{
      border-width: 15px 15px 7px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      @media #{$tablet-portrait} {
       height: 240px;
      }
    }
    .description{
      padding: 10px 15px 15px;
      background: $light_grey;
      border-bottom-right-radius: 7px;
      border-bottom-left-radius: 7px;
      h3{
        @include rem(font-size, 24px);
        margin-bottom: 10px;
        a{
          color: $black;
          text-decoration: none;
          transition: color .2s ease;
          &:hover,
          &:focus{
            color: $primary_color;
          }
        }
      }
    }
  }
}

.secondary-block{
  padding: 60px 0;
  text-align: center;
  @media #{$phone} {
    padding: 40px 0;
  }
  h2{
    margin-bottom: 45px;
    @include rem(font-size, 42px);
    @media #{$phone} {
      @include rem(font-size, 32px);
      margin-bottom: 20px;
    }
  }
  .col{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    ul{
      list-style: none;
      margin: 0 0 15px;
      padding: 0;
      @include rem(font-size, 20px);
      font-family: $font__main;
      color: $primary_color;
      li{
        a{
          color: $primary_color;
          transition: color .2s ease;
          &:hover,
          &:focus{
            color: $black;
          }
        }
      }
    }
  }
}