//
// Utility classes
// --------------------------------------------------


// Floats
// -------------------------

.clearfix {
  @include clearfix;
}
.center-block {
  @include center-block;
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}

.clearfix{
  &:before,
  &:after{
    content: "";
    display: table;
  }
  &:after{
    clear: both;
  }
}

// Toggling content
// -------------------------

// Note: Deprecated .hide in favor of .hidden or .sr-only (as appropriate) in v3.0.1
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.invisible {
  visibility: hidden;
}
.text-hide {
  @include text-hide;
}


// Hide from screenreaders and browsers
//
// Credit: HTML5 Boilerplate

.hidden {
  display: none !important;
}

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-justify        { text-align: justify; }
.text-nowrap         { white-space: nowrap; }

// Transformation
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }

// Container Class
.container{
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
.row{
  margin: 0 -15px;
}
.cell{
  padding: 0 15px;
}
.flex{
  display: flex;
  flex-wrap: wrap;
  &--reverse{
    flex-direction: row-reverse;
  }
}
.four-col{
  .card,
  .col{
    width: 25%;
    padding: 0 15px;
    margin-bottom: 30px;
    @media #{$tablet-portrait} {
      width: 33.33%;
    }
    @media #{$phone} {
      width: 50%;
    }
    @media #{$phone-portrait} {
      width: 100%;
    }
  }
}
.three-col{
  .col{
    width: 33.33%;
    padding: 0 15px;
    margin-bottom: 20px;
    @media #{$phone} {
      width: 50%;
    }
    @media #{$phone-portrait} {
      width: 100%;
    }
  }
}
.two-col{
  .col{
    width: 50%;
    padding: 0 15px;
    @media #{$phone-portrait} {
      width: 100%;
    }
  }
}
.col-medium{
  width: 50%;
}
.col-large{
  width: 70%;
  @media #{$tablet-portrait} {
    width: 100%;
  }
}
.bg-colored{
  background: $primary_color;
  color: $white;
}

.hvr-bounce-to-right {
  position: relative;
  transform: perspective(1px) translateZ(0);
  transition-property: color;
  transition-duration: 0.2s;
  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: $primary_color;
    transform: scaleX(0);
    transform-origin: 0 50%;
    transition-property: transform;
    transition-duration: 0.2s;
    transition-timing-function: ease-out;
  }
  &:hover{
    &:before{
      transform: scaleX(1);
      transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
    }
  }
}
.button{
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  padding: 9px 25px;
  display: inline-block;
  background: $primary_color;
  color: $white;
  border: 2px solid $primary_color;
  text-transform: uppercase;
  font-family: $font__main;
  letter-spacing: 1px;
  @extend .hvr-bounce-to-right;
  cursor: pointer;
  &:hover,
  &:focus{
    color: $primary_color;
    &:before{
      background: $white;
    }
  }
  &--secondary{
    border-color: $secondary_color;
    background: $secondary_color;
    &:hover,
    &:focus{
      color: $secondary_color;
      &:before{
        background: $white;
      }
    }
  }
  &--tertiary{
    border-color: $tertiary_color;
    background: $tertiary_color;
    &:hover,
    &:focus{
      color: $tertiary_color;
      &:before{
        background: $white;
      }
    }
  }
  &--border{
    border-color: $primary_color;
    color: $primary_color;
    background: transparent;
    &:hover,
    &:focus{
      color: $white;
      &:before{
        background: $primary_color;
      }
    }
  }
  &--border-primary{
    border-color: $white;
    color: $white;
    background: transparent;
    &:hover,
    &:focus{
      color: $black;
      &:before{
        background: $white;
      }
    }
  }
  .button-group & {
    margin: 0 8px 5px 0;
    &:last-child{
      margin: 0;
    }
  }
}
.more-link {
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  color: $primary_color;
  &:after{
    content: "";
    background: $primary_color;
    height: 2px;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: width .2s ease;
  }
  &:hover{
    &:after{
      width: 0;
    }
  }
}
/* Grid Classes */
.column{
  float: left;
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
  &.three{
    width: 25%;
  }
}
.gutter-top2 {
  margin-top: 20px;
}
.bg-alt{
  background: rgba(244, 244, 244, 0.35);
}
.overlay-link{
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  font-size: 0;
  text-indent: -9999em;
}

.back-to-top {
  cursor: pointer;
  position: fixed;
  bottom: 20px;
  right: 20px;
  display:none;
  background: $secondary-color;
  text-align: center;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  transition: background .3s ease;
  &:before{
    content: "";
    border: solid $white;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(-135deg);
    position: absolute;
    top: 16px;
    left: 16px;
  }
  &:hover{
    background: #030e3a;
  }
}


//preloader

.no-js #loader {
  display: none;
}

.js #loader {
  display: block;
  position: absolute;
  left: 100px;
  top: 0;
}

.se-pre-con {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url("assets/images/preloader.gif") center no-repeat #fff;
}