.bg-colored {
  background: #048dd4;
  color: #fff;
}

.hvr-bounce-to-right, .button {
  position: relative;
  transform: perspective(1px) translateZ(0);
  transition-property: color;
  transition-duration: 0.2s;
}

.hvr-bounce-to-right:before, .button:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #048dd4;
  transform: scaleX(0);
  transform-origin: 0 50%;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
}

.hvr-bounce-to-right:hover:before, .button:hover:before {
  transform: scaleX(1);
  transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
}

.button {
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  padding: 9px 25px;
  display: inline-block;
  background: #048dd4;
  color: #fff;
  border: 2px solid #048dd4;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  letter-spacing: 1px;
  cursor: pointer;
}

.button:hover, .button:focus {
  color: #048dd4;
}

.button:hover:before, .button:focus:before {
  background: #fff;
}

.button--secondary {
  border-color: #2e3092;
  background: #2e3092;
}

.button--secondary:hover, .button--secondary:focus {
  color: #2e3092;
}

.button--secondary:hover:before, .button--secondary:focus:before {
  background: #fff;
}

.button--tertiary {
  border-color: #ffce1a;
  background: #ffce1a;
}

.button--tertiary:hover, .button--tertiary:focus {
  color: #ffce1a;
}

.button--tertiary:hover:before, .button--tertiary:focus:before {
  background: #fff;
}

.button--border {
  border-color: #048dd4;
  color: #048dd4;
  background: transparent;
}

.button--border:hover, .button--border:focus {
  color: #fff;
}

.button--border:hover:before, .button--border:focus:before {
  background: #048dd4;
}

.button--border-primary {
  border-color: #fff;
  color: #fff;
  background: transparent;
}

.button--border-primary:hover, .button--border-primary:focus {
  color: #000;
}

.button--border-primary:hover:before, .button--border-primary:focus:before {
  background: #fff;
}

.button-group .button {
  margin: 0 8px 5px 0;
}

.button-group .button:last-child {
  margin: 0;
}

.more-link {
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  color: #048dd4;
}

.more-link:after {
  content: "";
  background: #048dd4;
  height: 2px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: width .2s ease;
}

.more-link:hover:after {
  width: 0;
}

/* Grid Classes */
.column {
  float: left;
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
}

.column.three {
  width: 25%;
}

.gutter-top2 {
  margin-top: 20px;
}

.bg-alt {
  background: rgba(244, 244, 244, 0.35);
}

.overlay-link {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  font-size: 0;
  text-indent: -9999em;
}

.back-to-top {
  cursor: pointer;
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: none;
  background: #2e3092;
  text-align: center;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  transition: background .3s ease;
}

.back-to-top:before {
  content: "";
  border: solid #fff;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(-135deg);
  position: absolute;
  top: 16px;
  left: 16px;
}

.back-to-top:hover {
  background: #030e3a;
}

.no-js #loader {
  display: none;
}

.js #loader {
  display: block;
  position: absolute;
  left: 100px;
  top: 0;
}

.se-pre-con {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url("assets/images/preloader.gif") center no-repeat #fff;
}

