/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #000;
  font-family: "Open Sans", sans-serif;
  line-height: 1.8;
  font-size: 16px;
  font-size: 1rem;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Oswald", sans-serif;
  font-weight: 700;
  margin: 0 0 20px 0;
  line-height: 1.2;
  text-rendering: optimizeLegibility;
}

h1 {
  font-size: 48px;
  font-size: 3rem;
}

@media screen and (max-width: 990px) {
  h1 {
    font-size: 38px;
    font-size: 2.375rem;
  }
}

h2 {
  font-size: 38px;
  font-size: 2.375rem;
}

@media screen and (max-width: 990px) {
  h2 {
    font-size: 32px;
    font-size: 2rem;
  }
}

h3 {
  font-size: 36px;
  font-size: 2.25rem;
}

@media screen and (max-width: 990px) {
  h3 {
    font-size: 30px;
    font-size: 1.875rem;
  }
}

h4 {
  font-size: 28px;
  font-size: 1.75rem;
}

@media screen and (max-width: 990px) {
  h4 {
    font-size: 26px;
    font-size: 1.625rem;
  }
}

h5 {
  font-size: 24px;
  font-size: 1.5rem;
}

@media screen and (max-width: 990px) {
  h5 {
    font-size: 22px;
    font-size: 1.375rem;
  }
}

h6 {
  font-size: 20px;
  font-size: 1.25rem;
}

@media screen and (max-width: 990px) {
  h6 {
    font-size: 18px;
    font-size: 1.125rem;
  }
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: #fff;
  /* Fallback for when there is no custom background color defined. */
}

blockquote, q {
  quotes: "" "";
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
}

hr {
  background-color: #fff;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

ul, ol {
  margin: 0 0 10px 25px;
  padding: 0;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */
}

table {
  margin: 0 0 1.5em;
  width: 100%;
}

p {
  margin: 0 0 20px;
}

a {
  color: #048dd4;
}

a:hover {
  color: #036698;
}

