/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50;
  padding: 10px 0;
  color: #fff;
  background: rgba(255, 255, 255, 0.3);
}

@media screen and (max-width: 767px) {
  .site-header {
    position: relative;
    background: #070862;
  }
}

.site-header .top-bar {
  padding: 0 0 15px;
}

.site-header .top-bar .flex {
  justify-content: flex-end;
}

.site-header .top-bar__contact a {
  margin-right: 20px;
  font-size: 22px;
  font-size: 1.375rem;
  font-family: "Oswald", sans-serif;
}

.site-header .top-bar__contact a:last-child {
  margin-right: 0;
}

.site-header__primary .container {
  max-width: 1270px;
}

@media screen and (max-width: 670px) {
  .site-header__primary .container > .flex {
    justify-content: center;
  }
}

.site-header__primary .flex {
  justify-content: space-between;
  align-items: center;
}

.site-header__primary .left-col {
  width: 165px;
  text-align: center;
  transition: width .2s ease;
}

@media screen and (max-width: 670px) {
  .site-header__primary .left-col {
    /* position: absolute;
        top: 10px;
        left: 50%;
        margin-left: -68px;*/
