    margin: 25px 0 20px;
  }
}

.site-header a {
  color: #fff;
  transition: color .3s ease;
  text-decoration: none;
}

.site-header a:hover, .site-header a:focus {
  color: #048dd4;
}

.site-header.is-sticky {
  background: #fff;
  color: #000;
  position: fixed;
