/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Primary Navigation */
.primary-navigation {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease;
}

#mobile-nav-trigger {
  opacity: 0;
  visibility: hidden;
  display: none;
  width: 24px;
  height: 18px;
  margin-left: 20px;
  top: 30px;
  left: 15px;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  float: right;
  z-index: 5;
