.gallery-list{
  .gallery-item{
    > a{
      padding: 5px;
      border-radius: 7px;
      background: $light_grey;
      display: block;
      img{
        display: block;
        border-radius: 7px;
        @media #{$phone-portrait} {
          width: 100%;
        }
      }
    }
  }
  &--large{
    margin-top: 60px;
    @media #{$phone} {
      margin-top: 30px;
    }
    .gallery-item{
      margin-bottom: 30px;
      @media #{$phone} {
        margin-bottom: 20px;
      }
      > a{
        height: 100%;
        img{
          object-fit: cover;
          height: 100%;
          width: 100%;
          @media #{$phone-portrait} {
            object-fit: inherit;
            height: auto;
            width: 100%;
          }
        }
      }
    }
  }
}