.site-header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50;
  padding: 10px 0;
  color: $white;
  background: rgba(255, 255, 255, .3);
  @media #{$phone} {
   position: relative;
    background: $secondary_color_dark;
  }
  .top-bar{
    padding: 0 0 15px;
    .flex{
      justify-content: flex-end;
    }
    &__contact {
      a{
        margin-right: 20px;
        @include rem(font-size, 22px);
        font-family: $font__main;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
  &__primary{
    .container{
      max-width: 1270px;
      > .flex{
        @media screen and (max-width: 670px) {
          justify-content: center;
        }
      }
    }
    .flex{
      justify-content: space-between;
      align-items: center;
    }
    .left-col{
      width: 165px;
      text-align: center;
      transition: width .2s ease;
      @media #{$tablet-portrait} {
       // width: 30%;
      }
      @media screen and (max-width: 670px){
       /* position: absolute;
        top: 10px;
        left: 50%;
        margin-left: -68px;*/
        width: 136px;
      }
      @media screen and (max-width: 499px) {
       // margin-left: -130px;
      }
      @media screen and (max-width: 400px) {
        //margin-left: -130px;
        //width: 180px;
      }
      .site-logo{
        display: block;
        background: #fff;
        padding: 5px;
        img{
          display: block;
        }
      }
    }
    .right-col{
      width: calc(100% - 165px);
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: flex-end;
      @media #{$tablet-portrait} {
        width: calc(100% - 265px);
        margin-top: 20px;
      }
      @media screen and (max-width: 670px) {
        width: 100%;
        justify-content: center;
      }
    }
  }
  ul{
    list-style: none;
    margin: 0;
    &.quick-info{
      @media only screen and (max-width: 930px){
        margin-top: 20px;
      }
      @media #{$phone} {
       margin-top: 0;
      }
    }
    &.secondary-menu{
      @media #{$header} {
        margin: 25px 0 20px;
      }
    }
  }
  a{
    color: $white;
    transition: color .3s ease;
    text-decoration: none;
    &:hover,
    &:focus{
      color: $primary_color;
    }
  }
  &.is-sticky{
    background: $white;
    color: $black;
    position: fixed;
    box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.23);
    @media #{$tablet-portrait} {
    position: relative;
    }
    a{
      color: $black;
    }
    .menu-holder{
      .primary-navigation{
        display: inline-block;
      }
    }
    .left-col{
      width: 145px;
    }
  }

}

.social-media{
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  line-height: 1;
  list-style: none;
  li{
    margin-right: 10px;
    &:last-child{
      margin-right: 0;
    }
    a{
      @include rem(font-size, 18px);
      height: 32px;
      width: 32px;
      border-radius: 50%;
      text-align: center;
      overflow: hidden;
      color: $black;
      background: $white;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      &:hover{
        background: $primary_color;
        color: $white;
      }
    }
    &.phone, &.email{
      //display: none;
      @media #{$tablet-portrait} {
        span{
          display: none;
        }
      }
    }
  }
}
