/*!== Brand Colors == */

$white: #fff;
$primary_color: #048dd4;
$primary_color_dark: #036698;
$secondary_color: #2e3092;
$secondary_color_dark:#070862;
$tertiary_color:#ffce1a;
$tertiary_color_dark: #e4b300;
$color__brand-success: #5cb85c;
$color__brand-alert: #ff0000;
$light_white: #808080;
$grey: #787a7b;
$light_grey:#f2f2f2;
$black:#000;
$light-black:#1f2229;

/*!== Typography == */

$font__main: 'Oswald', sans-serif;
$font__main_alt: 'Open Sans', sans-serif;
$font__line-height-body: 1.8;
$font__line-heading: 1.2;

/*== Theme Colors == */

$color__text-screen: #21759b;
$color__text-input: #666;
$color__text-input-focus: #111;
$color__link: royalblue;
$color__link-visited: purple;
$color__link-hover: midnightblue;
$color__text-main: #404040;

$color__border-button: #ccc #ccc #bbb;
$color__border-button-hover: #ccc #bbb #aaa;
$color__border-button-focus: #aaa #bbb #bbb;
$color__border-input: #ccc;
$color__border-abbr: #666;
$alert-color:#9f3a38;
$alert-color-bg:#fff6f6;
$alert-color-border:#e0b4b4;

$success-color:#2c662d;
$success-color-bg:#fcfff5;
$success-color-border:#a3c293;

// Media Queries
$header-width: 990px;
$tablet-width: 768px;
$tablet-portrait-width: 1023px;
$phone-width: 767px;
$phone-portrait-width: 567px;


$header: 'screen and (max-width: #{$header-width})';
$tablet: 'screen and (max-width: #{$tablet-width})';
$tablet-portrait: 'screen and (max-width: #{$tablet-portrait-width})';
$phone: 'screen and (max-width: #{$phone-width})';
$phone-portrait: 'screen and (max-width: #{$phone-portrait-width})';
