/* Contact Block Styling */
.contact-block{
  padding: 60px 0;
  @media #{$phone} {
    padding: 40px 0;
  }
  &__intro{
    max-width: 80%;
    @media #{$tablet-portrait} {
      max-width: 100%;
    }
  }
  .cell{
    padding-bottom: 20px;
  }
  .form-col{
    @media #{$phone} {
      margin-bottom: 20px;
    }
  }
  .map-col,
  .form-col{
    width: 50%;
    padding: 0 15px;
    @media #{$phone} {
      width: 100%;
    }
  }
  .map-col{
    iframe{
      display: block;
      width: 100%;
    }
  }
  .contact-alt{
    margin-top: 15px;
    @include rem(font-size, 20px);
    font-family: $font__main;
    font-weight: 600;
    a{
      text-decoration: none;
      color: $black;
      transition: color .2s ease;
      &:hover,
      &:focus{
        color: $primary_color;
      }
    }
    .address{
      margin-bottom: 10px;
      line-height: 1.3;
    }
  }
}
.quick-quote{
  padding: 70px 0;
  .col-large{
    margin: 0 auto;
    h3{
      text-align: center;
      margin-bottom: 25px;
    }
  }
}